import React, { Component } from 'react';
import Result from './components/Result'
import './App.css';
/*import  Example  from './components/Example';*/
import  SearchBar from './components/SearchBar';
import Timetable2 from './components/Timetable2';
import moment from "moment";
import {Container} from 'reactstrap';

import Modal from 'react-modal';

class App extends Component {

  returnResult(result) {
    this.setState({result:result});
    for (course in result.data){
      for (time in course.session){
        console.log(time);
        console.log('Day:', parseInt(time.substring(1,3), 2));
      }
    }
    // course_time = result.data.substring(start, end)
    // console.log('Get Result..\n', result.data);
  }

  addEvent(course) {
    var events = this.state.events;
    var newEvent =
        {
          id: 2,
          name: '',
          type: '',
          location: '',
          startTime: '2000-01-01T00:00:00',
          endTime: '2000-01-01T00:00:00',
          priority: 1000
        }

    for (var key in events) {
      if (events.hasOwnProperty(key)) {
          var courses = events[key];
          if(courses.length !== 0 ) {
            for (var i=0; i<courses.length; i++){
              /* Priority 越小約高 */
              newEvent.priority = Math.min(newEvent.priority, courses[i].priority-1);
            }
          }
      }
    }
    /* Get courseInfo and set */
    newEvent.name = 'test';
    const index = 11;
    newEvent.startTime = moment(newEvent.startTime.substr(0, index) + '08' + newEvent.startTime.substr(index+2));
    newEvent.endTime   = moment(newEvent.endTime.substr(0, index)   + '10' + newEvent.endTime.substr(index+2));
    events.monday.push(newEvent);

    this.setState({events:events})
  }

  deleteEvent(course) {
    return 0;
  }


  constructor() {
    super();

    this.returnResult = this.returnResult.bind(this);
    this.addEvent     = this.addEvent.bind(this);
    this.deleteEvent  = this.deleteEvent.bind(this);

    var events= {
      monday: [
        {
          id: 1,
          name: '軟體工程',
          type: 'custom',
          location: '(大智樓)',
          startTime: moment('2000-01-01T13:00:00'),
          endTime: moment('2000-01-01T16:00:00'),
          priority: 1
        }
      ],

      tuesday: [
        {
          id: 1,
          name: 'aaa',
          type: 'custom',
          location: '(大仁樓)',
          startTime: moment('2000-01-01T08:00:00'),
          endTime: moment('2000-01-01T11:00:00'),
          priority: 3
        },

        {
          id: 1,
          name: 'bbb',
          type: 'custom',
          location: '(大仁樓)',
          startTime: moment('2000-01-01T10:00:00'),
          endTime: moment('2000-01-01T12:00:00'),
          priority: 2
        },

        {
          id: 1,
          name: 'ccc',
          type: 'custom',
          location: '大仁樓',
          startTime: moment('2000-01-01T11:00:00'),
          endTime: moment('2000-01-01T13:00:00'),
          priority: 4
        },
      ],
      wednesday: [],
      thursday: [],
      friday: []
    };

    this.state = {
      events : events,
      result : "",
    }
    this.addEvent();
  }

  componentDidMount() {
      Modal.setAppElement(this.el);
  }



  render() {
    return (
      <div ref={ref => this.el = ref}>
        <SearchBar returnResult={this.returnResult}/>
        <Timetable2 events={this.state.events}/>
        <Container>
          <Result addCourse={this.addEvent} searchResult={this.state.result}/>
        </Container>
      </div>
    );
  }
}

export default App;



