import React from 'react';
import Modal from 'react-modal';
import Timetable from 'react-timetable-events';
import classnames from 'classnames';

import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, Container,CardTitle, CardText, Row, Col } from 'reactstrap';
/*
import moment from "moment";
import {Collapse0,Collapse,Input,Button,Row, Col,} from 'reactstrap';
import { slide as Slide } from 'react-burger-menu'
import right_arrow_icon from './right-arrow.svg'
*/

import SlidingPane from 'react-sliding-pane';
import 'react-sliding-pane/dist/react-sliding-pane.css';
import left_arrow_icon from './left-arrow.svg'





export default class Timetable2 extends React.Component {

  handleSlide() {
    this.setState({ slide: !this.state.slide });
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  constructor(props){
    super();
    var events = props.events;
    var t = this.getEarliestLatest(events);
    var conflict_courses = this.removeConflict(events);
    this.toggle = this.toggle.bind(this);

    this.state = {
      activeTab: '1',
      isPaneOpen : false,
      earliest: t[0],
      latest: t[1],
      slide : false,
      events: events,
      conflict_courses: conflict_courses,
    }

  }

  removeConflict(events) {
    var event_list = [];
    for (var key in events) {
      if (events.hasOwnProperty(key)) {
          var courses = events[key];
          if(courses.length !== 0 ) {
            for (var i=0; i<courses.length; i++){
              var s_time = courses[i].startTime._i.slice(-8, -6);
              var e_time = courses[i].endTime._i.slice(-8, -6);
              event_list.push({'day':key, 's_time': s_time, 'e_time':e_time, 'priority':courses[i].priority, 'name':courses[i].name});
            }
          }
      }
    }
    event_list.sort(function(a,b) {return (a.priority < b.priority) ? 1 : ((b.priority > a.priority) ? -1 : 0);} );
    var all_conflict_courses = []
    for(i=0; i<event_list.length; i++) {
      var cur_event = event_list[i];
      for(var j=i+1; j<event_list.length; j++) {
        if(cur_event.day === event_list[j].day) {
          if(this.isOverlap(cur_event.s_time, cur_event.e_time, event_list[j].s_time, event_list[j].e_time)) {
            var day = cur_event.day;
            var remove_name = ((cur_event.priority < event_list[j].priority)?cur_event.name:event_list[j].name);
            events[day] = events[day].filter(function(el) {return el.name !== remove_name;});
          }
        }
      }
    }
    return all_conflict_courses;

  }

  isOverlap(start_1, end_1, start_2, end_2){
    if( (start_2 > start_1 && start_2 < end_1)
      ||  (end_2 > start_1 && end_2 < end_1) )
    return true;

    else
      return false;

  }

  getEarliestLatest(events) {
    var earliest = 10;
    var latest   = 11;
    for (var key in events) {
      if (events.hasOwnProperty(key)) {
          var courses = events[key];
          if(courses.length !== 0 ) {
            for (var i=0; i<courses.length; i++){
              var s_time = courses[i].startTime._i.slice(-8, -6);
              var e_time = courses[i].endTime._i.slice(-8, -6);
              s_time = parseInt(s_time, 10);
              e_time = parseInt(e_time, 10);
              if(s_time < earliest) earliest = s_time;
              if(e_time > latest) latest = e_time;
            }
          }
      }
  }
    return [earliest,latest];
}

  componentDidMount() {
    Modal.setAppElement(this.el);
  }

  render() {
        return(
        <div ref={ref => this.el = ref}>
        <div id={'show_timetabel_block'} onClick={() => this.setState({ isPaneOpen: true })} >
          <p className='myp'>顯示</p>
          <img src={left_arrow_icon} id='img' alt="description"/>
          <p className='myp'>課表</p>
        </div>
            <SlidingPane
                className={'some-custom-class'}
                overlayClassName='some-custom-overlay-class'
                isOpen={ this.state.isPaneOpen }
                title='您的課表'
                width='70vw'
                subtitle='Optional subtitle.'
                onRequestClose={ () => {
                    // triggered on "<" on left top click or on outside click
                    this.setState({ isPaneOpen: false });
                } }>

              <Nav tabs>
                  <NavItem id="timetable_0">
                    <NavLink
                      className={classnames({ active: this.state.activeTab === '1' })}
                      onClick={() => { this.toggle('1'); }}
                    >
                      未衝堂課表

                    </NavLink>
                  </NavItem>
                  <NavItem id="timetable_1">
                    <NavLink
                      className={classnames({ active: this.state.activeTab === '2' })}
                      onClick={() => { this.toggle('2'); }}
                    >
                      衝堂課列表
                    </NavLink>
                  </NavItem>
                </Nav>
                <TabContent activeTab={this.state.activeTab}>
                  <TabPane tabId="1">
                    <Row>
                      <Col sm="12">

                        {/*    Timetable      */}
                        <div>
                          <Timetable hoursInterval={[ this.state.earliest, this.state.latest ]} id={'Timetable'} events={ this.state.events }/>
                        </div>

                      </Col>
                    </Row>
                  </TabPane>
                  <TabPane tabId="2">

                    <Row>
                      <Col sm="6">
                        <Card body>
                          <CardTitle>Special Title Treatment</CardTitle>
                          <CardText>With supporting text below as a natural lead-in to additional content.</CardText>
                          <Button>Go somewhere</Button>
                        </Card>
                      </Col>
                      <Col sm="6">
                        <Card body>
                          <CardTitle>Special Title Treatment</CardTitle>
                          <CardText>With supporting text below as a natural lead-in to additional content.</CardText>
                          <Button>Go somewhere</Button>
                        </Card>
                      </Col>
                    </Row>
                  </TabPane>
                  
                </TabContent>




            </SlidingPane>
        </div>
      )
    }
}



