import React from 'react';
import Pagination from './Pagination.js';
import { Button, ButtonGroup } from 'reactstrap';
import { Table } from 'reactstrap';

export default class Course extends React.Component {
  constructor(props) {
    var test = {'data': [{'crse': '正規語言', 'instructor':['test'], 'place': ["大仁樓200102"], 'point': 3, 'session':["11111111"] },
                       {'crse':   '正規語言2', 'instructor':['fuck'], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse':  '正規語言3', 'instructor':['aaa'], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                      {'crse':  '正規語言4', 'instructor':['bbb'], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse':  '正規語言5', 'instructor':["ccc"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '9', 'instructor':['ddd'], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '10', 'instructor':['eee'], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                        {'crse': '4', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': ' 5', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '6', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '7', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '8', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '9', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '10', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '2', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '3', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '4', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '5', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '6', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '7', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '8', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '9', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '10', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '2', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '3', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '4', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '5', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },
                       {'crse': '6', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] },

                       {'crse': '11', 'instructor':["陳正佳"], 'place': ['大仁樓200102'], 'point': 3, 'session':["11111111"] }
            ]}
    super(props);
    this.onClickUpdate = this.onClickUpdate.bind(this);
    this.slice = this.slice.bind(this);
    this.state = {course: test.data, courseSlice: [], pageCount: 0, courseSelected: []}

    var rtn = this.slice(-1)
    this.state.courseSlice = rtn[1]
    this.state.pageSize = rtn[2]
  }

  onClickUpdate(sortMethod){
    var rtn = this.slice(sortMethod)
    this.setState({course: rtn[0], courseSlice: rtn[1], pageCount: rtn[2]})
    document.getElementsByClassName("Page1")[0].style.display = "block";
    for(var i = 2; i <= this.state.pageSize; i++)
        document.getElementsByClassName("Page" + i)[0].style.display = "none";
  }

  addCourse(newCourse){
    var selected = this.state.courseSelected
    if(selected.includes(newCourse)){
      delete selected[selected.indexOf(newCourse)]
    }
    else {
        selected.push(newCourse)
    }
    this.setState({courseSelected: selected})
  }
  slice(sortMethod){
    const courseSize = 10
    const info = ['crse', 'instructor', 'place', 'point', 'session']
    var course = this.state.course
    var courseSlice = []
    var pageCount = 0

    if(sortMethod !== -1){
      course = course.sort(function(x, y){
        return ((x[info[sortMethod]] < y[info[sortMethod]]) ? -1 : ((x[info[sortMethod]] > y[info[sortMethod]]) ? 1 : 0));
      })
    }
    for(var i = 0; i < course.length; i += courseSize){
      courseSlice.push(course.slice(i, i + courseSize))
      pageCount += 1
    }
    return [course, courseSlice, pageCount]
  }

  render(){
    return(
      <div>
        <ButtonGroup>
          <Button className='sortTag' color = "link" onClick={() => { this.onClickUpdate(0) }}>課程名稱</Button>
          <Button className='sortTag' color = "link" onClick={() => { this.onClickUpdate(1) }}>教師名稱</Button>
          <Button className='sortTag' color = "link" onClick={() => { this.onClickUpdate(2) }}>上課地點</Button>
          <Button className='sortTag'color = "link" onClick={() => { this.onClickUpdate(3) }}>學分數量</Button>
        </ButtonGroup>
        {
        this.state.courseSlice.map((frag) => {
          return <Table hover
          className={('Page' + (this.state.courseSlice.indexOf(frag) + 1))}
          style={{display: (this.state.courseSlice.indexOf(frag) == 0)? 'block' : 'none'}}
          >
            <tbody>
            {

              frag.map((course) => {
                return <tr scope="row"
                       onClick={() => { this.addCourse(course)}}
                       style={{color: this.state.courseSelected.includes(course)? 'blue' : 'green'}}
                       >
                      <td>{course.crse}</td>
                      <td>{course.instructor}</td>
                      <td>{course.place}</td>
                      <td>{course.point}</td>
                      <td>{course.session}</td>
                      </tr>
            })}
            </tbody>
          </Table>
          })}
          <Pagination pageSize={this.state.pageSize}/>
          <Button className='submit' color= 'link' onClick= {() => {}}>提交</Button>
      </div>
    );
  }
}



